//other
import { STOK_TAKE_UPLOAD_JSON } from "apiServices/action";
import { submitCsv } from "apiServices";
import moment from "moment";

export const showErrorsOnly = ({
  csvData,
  csvField,
  isShowErrorOnly,
  setIsShowErrorOnly,
  setIsHiddenRows,
  setCheckingCsv,
}) => {
  // ;
  let tmp_arr_false = [];
  let tmp_arr_true = [];
  csvData.map((data, index) => {
    let stat = true;
    let i = index + 1;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      if (data[key] === false) {
        stat = false;
      }
    });

    if (stat === false) {
      if (!tmp_arr_false.includes(i)) {
        tmp_arr_false.push(i);
      }
    } else {
      if (!tmp_arr_true.includes(i)) {
        tmp_arr_true.push(i);
      }
    }
  });

  //return array
  let ret = [];
  if (isShowErrorOnly === true) {
    //true = delete all row
    ret = tmp_arr_true.filter((x) => !tmp_arr_false.includes(x));
    if (ret.length == csvData.length) {
      return 0;
    }
  } else {
    ret = [];
  }
  setIsHiddenRows(ret);
  setIsShowErrorOnly(!isShowErrorOnly); //untuk button show all atau error only
  setCheckingCsv(true);
};

export const deleteErrorsField = ({
  csvData,
  csvField,
  dispatch,
  setCheckingCsv,
}) => {
  let new_arr = [];
  let tmp = null;
  csvData.map((data, index) => {
    let stat = true;
    csvField.map((dataColumn, indexColumn) => {
      let key = dataColumn.dataField + "Status";
      let i = index + 1;
      if (data[key] === false) {
        stat = false;
      }
    });
    if (stat === true) {
      new_arr.push(data);
    }
  });

  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: new_arr });
  setCheckingCsv(true);
};

export const setCsvData = ({ param, dispatch, setCheckingCsv }) => {
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvData: param });
  setCheckingCsv(true);
};

export const checking_error = ({
  csvData,
  csvField,
  setIsValidate,
  setCheckingCsv,
}) => {
  if (csvData === undefined) {
    return 0;
  }
  let stats = true;
  csvData.map((data, index) => {
    csvField.map((dataField, indexField) => {
      let status = data[dataField.dataField + "Status"];
      if (status === false) {
        stats = false;
      }
    });
  });
  if (stats === false) {
    setIsValidate(false);
  } else {
    setIsValidate(true);
  }
  setCheckingCsv(false);
};

const setHeader = (csvData) => {
  //get time - created at
  let time = new Date().getTime();
  let d = new Date(time);
  let n = d.getMilliseconds();
  let list_area = [];
  let return_data = [];
  let warehouse_source = csvData[0].warehouseSource || "";

  csvData.map((data, index) => {
    //in outbound csv upload only check area source
    if (!list_area.includes(data.areaSource)) {
      return_data.push({
        id: index,
        warehouseSource: warehouse_source,
        warehouseDestination: warehouse_source,
        areaSource: data.areaSource,
        areaDestination: data.areaSource,
        list: [],
        createdAt: n,
      });
      list_area.push(data.areaSource);
    }
  });
  return return_data;
};

const getSKU = (csvData) => {
  let return_data = [];
  let warehouse_source = csvData[0].warehouseSource || "";

  csvData.map((data, index) => {
    let available = false;
    return_data.forEach((header, idx) => {
      if (
        header &&
        header.skuUuid == data.skuUuid &&
        header.warehouseSource == data.warehouseSource &&
        header.areaSource == data.areaSource
      ) {
        available = true;
      }
    });

    if (!available) {
      return_data.push({
        warehouseSource: warehouse_source,
        areaSource: data.areaSource,
        skuUuid: data.skuUuid,
        skuNumber: data.skuNumber,
        unitOfMeassures: data.uom,
        photo: data.photos,
        poList: [],
      });
    }
  });
  return return_data;
};

const checkGrade = (value) => {
  if (!value) {
    return;
  }
  if (value.toUpperCase() == "SEMI WASTE") {
    return "semi_waste";
  } else if (value.toUpperCase() == "WASTE") {
    return "waste";
  } else {
    return null;
  }
};

const groupingPO = (csvData, skuList) => {
  let return_data = [];
  let warehouse_source = csvData[0].warehouseSource || "";

  csvData.map((data, index) => {
    let available = false;
    let header_index = "";

    return_data.forEach((header, idx) => {
      if (
        header &&
        header.areaSource == data.areaSource &&
        header.warehouseSource == data.warehouseSource &&
        header.skuUuid == data.skuUuid &&
        header.wmsPoSkuCode == data.wmsPoSkuCode &&
        header.expiryDate == data.expiryDate
      ) {
        available = true;
        header_index = idx;
      }
    });

    if (!available) {
      return_data.push({
        skuUuid: data.skuUuid || "",
        areaSource: data.areaSource || "",
        warehouseSource: warehouse_source || "",
        inboundDate: data.inboundDate || "",
        supplierId: data.supplierUuid || "",
        supplierName: data.supplierName || "",
        wmsPoSkuCode: data.wmsPoSkuCode || "",
        expiryDate:
          moment(data.expiryDateCol, "DD-MM-YYYY").toDate().getTime() || "",
        codeGrade: data.skuUuid || "",
        itemList: [],
      });
      header_index = return_data.length - 1;
    }

    //check ripeness
    let newRipeness = data.newRipeness;
    if (data.newRipeness == "") {
      newRipeness = data.ripenessLevel || 0;
    }

    //check grade
    let newGrade = checkGrade(data.newGrade);
    if (newGrade === null) {
      newGrade = data.newGradeUuid;
    }

    return_data[header_index].itemList.push({
      qty: data.qty || 0,
      areaSource: data.areaSource,
      expiryDateText: data.expiryDateCol,
      expiryDate:
        moment(data.expiryDateCol, "DD-MM-YYYY").toDate().getTime() || "",
      skuNumber: data.skuNumber,
      wmsPoSkuCode: data.wmsPoSkuCode,
      ripeness: { label: data.newRipeness || "", value: newRipeness || 0 },
      grade: { label: data.newGrade || "", value: newGrade || data.skuUuid },
      areaName: data.areaName || "",
    });
  });
  return return_data;
};

const setJobPost = (header, skuList, poGroup) => {
  let return_data = [];

  //insert PO to SKUList
  skuList.map((skuListData, skuListIdx) => {
    poGroup.map((poGroup, poGroupIndex) => {
      if (
        poGroup &&
        poGroup.areaSource == skuListData.areaSource &&
        poGroup.warehouseSource == skuListData.warehouseSource &&
        poGroup.skuUuid == skuListData.skuUuid
      ) {
        skuList[skuListIdx]["poList"].push(poGroup);
      }
    });
  });

  //insert sku to Header
  header.map((headerData, headerIdx) => {
    skuList.map((skuListData, skuListDataIdx) => {
      if (
        poGroup &&
        headerData.areaSource == skuListData.areaSource &&
        headerData.warehouseSource == skuListData.warehouseSource
      ) {
        header[headerIdx]["list"].push(skuListData);
      }
    });
  });

  return header;
};

const setResult = (result) => {
  let arrayResult = [];
  let i = 1;
  result.data.list.map((dataHeader, indexHeader) => {
    dataHeader.poList.map((poHeader, indexPo) => {
      let poSkuCode = poHeader.wmsPoSkuCode;
      let ripenessLevel = poHeader.ripenessLevel;
      let grade = poHeader.codeGrade;
      poHeader.itemList.map((data, index) => {
        let isStatus = "Failed";
        if (data.isSuccess) {
          isStatus = "Success";
        }
        arrayResult.push({
          id: i,
          uploadStatus: isStatus || "",
          uploadStatusReason: data.message,
          wmsPoSkuCode: poSkuCode || "",
          skuNumber: data.skuNumber || "",
          expiryDateCol: data.expiryDateText || "",
          qty: data.qty || 0,
          grade: grade || "",
          newGrade: data.grade.label || "",
          areaName: data.areaName || "",
          newRipeness: data.ripeness.label || "",
        });
        i++;
      });
    });
  });
  return arrayResult;
};

export const submit = async ({
  csvData,
  csvField,
  dispatch,
  selectorStatus,
  setIsShowModal,
  setIsSubmitText,
  setSubmitStatus,
}) => {
  setIsSubmitText("Loading...");
  console.clear();

  //set header
  let header = await setHeader(csvData);

  //get list sku
  let skuList = await getSKU(csvData);

  //grouping PO
  let poGroup = await groupingPO(csvData, skuList);

  //set JobPost
  let jobPost = await setJobPost(header, skuList, poGroup);

  //submit
  let execute = await submitCsv({
    dataCsv: jobPost,
    setSubmitStatus,
    dispatch,
    transaction: "stockTake",
  });
  if (execute == "error") {
    return 0;
  }

  //set result
  let result = await setResult(execute);
  await dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvDataResult: result });

  //renew schema for result
  let newSchema = [];
  newSchema.push({
    dataField: "uploadStatus",
    text: "Upload Status",
    type: "string",
    sort: true,
  });
  csvField.map((data, index) => {
    newSchema.push(data);
  });
  await dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvFieldResult: newSchema });

  setIsShowModal(false);
  setIsSubmitText("Submit");
  setSubmitStatus(true);
};

export const submit_old = async ({
  csvData,
  csvField,
  dispatch,
  setIsShowModal,
  setIsSubmitText,
  setSubmitStatus,
}) => {
  setIsSubmitText("Loading...");
  let jobHeader = [];
  let poHeader = [];
  let poBody = [];

  //0 get time
  let time = new Date().getTime();
  let d = new Date(time);
  let n = d.getMilliseconds();

  //1. set header
  jobHeader = [
    {
      jobPost: {
        id: 0,
        warehouseSource: csvData[0].warehouseSource || "",
        warehouseDestination: csvData[0].warehouseSource || "",
        areaSource: csvData[0].areaSource || "",
        areaDestination: csvData[0].areaSource || "",
        list: null,
      },
    },
  ];

  //2. get sku
  let sku_uuid_array = [];
  let sku_number_array = [];
  let uom_array = [];
  let photos_array = [];
  let sku_data = [];
  csvData.map((data, index) => {
    if (!sku_uuid_array.includes(data.skuUuid)) {
      sku_uuid_array.push(data.skuUuid);
      sku_data.push({
        skuNumber: data.skuNumber,
        skuUuid: data.skuUuid,
        uom: data.uom,
        photo: data.photo,
      });
    }
  });

  //3. get PO per sku
  let po_array = [];
  sku_data.forEach((data_sku, index_sku) => {
    if (data_sku === null || data_sku === undefined || data_sku === "") {
      return false;
    }

    let tmp_po = {};
    tmp_po.skuUuid = data_sku.skuUuid;
    tmp_po.skuNumber = data_sku.skuNumber;
    tmp_po.unitOfMeassures = data_sku.uom;
    tmp_po.photo = data_sku.photo;
    tmp_po.poList = [];
    csvData.map((data, index) => {
      if (data.skuNumber !== data_sku.skuNumber) {
        return false;
      }

      let tmp_po_array = {};
      tmp_po_array.wmsPoSkuCode = data.wmsPoSkuCode || "";
      tmp_po_array.supplierUuid = data.supplierUuid || "";
      tmp_po_array.supplierName = data.supplierName || "";
      tmp_po_array.inboundDate = data.inboundDate || 0;
      tmp_po_array.expiryDate = data.expiryDateCol || 0;
      tmp_po_array.skuUuid = data.skuUuid;

      //grade
      tmp_po_array.gradeLabel = data.newGrade;
      if (data.newGrade.toUpperCase() == "WASTE") {
        tmp_po_array.grade = "waste";
        tmp_po_array.newUom = data_sku.uom;
      } else if (data.newGrade.toUpperCase() == "SEMI WASTE") {
        tmp_po_array.grade = "semi_waste";
        tmp_po_array.newUom = data_sku.uom;
      } else {
        tmp_po_array.grade = data.newGradeUuid || data_sku.skuUuid; //if grade null then grade =s kuUuid
        tmp_po_array.newUom = data.newGradeUom || data_sku.uom; //if grade null then grade =s kuUuid
      }

      tmp_po_array.qty = data.qty || 0;
      tmp_po_array.ripeness = data.ripenessLevel || 0;
      tmp_po_array.newRipeness = data.newRipeness || "";
      tmp_po_array.areaName = data.areaName || "";
      tmp_po_array.skuNumber = data.skuNumber || "";
      tmp_po_array.warehouseCode = data.warehouseCode || "";
      tmp_po_array.expiryDate = data.expiryDateCol || "";
      tmp_po.poList.push(tmp_po_array);
    });
    po_array.push(tmp_po);
  });

  //merapikan  po_array
  let po_array_fix = [];
  po_array.forEach((data, index) => {
    let tmp = {};
    let po_code = [];
    let po_list = [];
    let header_po = [];
    data.poList.map((data_po, index_po) => {
      let ripeness = "";
      let newRipeness = "";

      //check if header already set or not
      let header_available = false;
      let header_index = null;
      header_po.forEach((header, idx) => {
        if (
          header &&
          header.ripenessLevel == data_po.ripeness &&
          header.wmsPoSkuCode == data_po.wmsPoSkuCode &&
          header.expiryDate == data_po.expiryDate
        ) {
          header_available = true;
          header_index = idx;
        }
      });

      //if header not available then set
      if (header_available === false) {
        header_po.push({
          ripenessLevel: data_po.ripeness,
          wmsPoSkuCode: data_po.wmsPoSkuCode,
          expiryDate: data_po.expiryDate,
        });
        header_index = header_po.length - 1;

        let tmp_po_array = {};
        tmp_po_array.wmsPoSkuCode = data_po.wmsPoSkuCode || "";
        tmp_po_array.supplierId = data_po.supplierUuid || "";
        tmp_po_array.supplierName = data_po.supplierName || "";
        tmp_po_array.codeGrade = data_po.skuUuid || "";
        tmp_po_array.ripenessLevel = data_po.ripeness || 0;

        //date
        tmp_po_array.inboundDate = data_po.inboundDate || 0;
        tmp_po_array.expiryDate =
          moment(data_po.expiryDate, "DD-MM-YYYY").toDate().getTime() +
            25200000 || "";
        tmp_po_array.itemList = [];
        po_list.push(tmp_po_array);
      }
      //end check header

      //new sku ripeness
      //if new ripeness == null then new ripeness=ripenessLevel
      if (data_po.newRipeness == "") {
        newRipeness = data_po.ripeness;
      } else {
        newRipeness = data_po.newRipeness;
      }

      let tmp_itemlist = {};
      tmp_itemlist.grade = { label: data_po.gradeLabel, value: data_po.grade };
      tmp_itemlist.ripeness = {
        label: data_po.newRipeness,
        value: newRipeness,
      };
      tmp_itemlist.qty = data_po.qty || 0;
      tmp_itemlist.pack = data_po.pack || 0;
      tmp_itemlist.uom = data_po.newUom || "";
      tmp_itemlist.areaName = data_po.areaName || "";
      tmp_itemlist.skuNumber = data_po.skuNumber || "";
      tmp_itemlist.warehouseCode = data_po.warehouseCode || "";
      tmp_itemlist.expiryDate = data_po.expiryDate || "";
      po_list[header_index].itemList.push(tmp_itemlist);
    });
    tmp.skuUuid = data.skuUuid;
    tmp.skuNumber = data.skuNumber;
    tmp.unitOfMeassures = data.unitOfMeassures;
    tmp.photo = data.photo;
    tmp.poList = po_list;
    po_array_fix.push(tmp);
  });
  //final
  jobHeader[0].jobPost.list = po_array_fix;
  let ret = await submitCsv({
    dataCsv: jobHeader,
    setSubmitStatus,
    dispatch,
    transaction: "stockTake",
  });
  setIsShowModal(false);
  setIsSubmitText("Submit");
  if (ret == "error") {
    return 0;
  }

  //set dispatch result
  let arrayResult = [];
  let i = 1;
  await ret.jobPost.list.map((dataHeader, indexHeader) => {
    dataHeader.poList.map((poHeader, indexPo) => {
      let poSkuCode = poHeader.wmsPoSkuCode;
      let ripenessLevel = poHeader.ripenessLevel;
      poHeader.itemList.map((data, index) => {
        let isStatus = "Failed";
        if (data.isSuccess) {
          isStatus = "Success";
        }

        arrayResult.push({
          id: i,
          warehouse: data.warehouseCode || "",
          areaName: data.areaName || "",
          wmsPoSkuCode: poSkuCode || "",
          newGrade: data.grade.label || "",
          ripeness: ripenessLevel,
          newRipeness: data.ripeness.label || "",
          skuNumber: data.skuNumber || "",
          expiryDate: data.expiryDate || "",
          uploadStatusReason: data.message,
          uploadStatusErrorCode: data.errorCode,
          uploadStatus: isStatus,
          qty: data.qty,
        });
        i++;
      });
    });
  });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvDataResult: arrayResult });

  //renew schema
  let newSchema = [];
  csvField.map((data, index) => {
    if (index == 1) {
      newSchema.push({
        dataField: "uploadStatus",
        text: "Upload Status",
        type: "string",
        sort: true,
      });
    } else {
      newSchema.push(data);
    }
  });
  dispatch({ type: STOK_TAKE_UPLOAD_JSON, csvFieldResult: newSchema });
  setSubmitStatus(true);
};
